















































import {Vue} from 'vue-property-decorator';
import {BrowserQRCodeReader, VideoInputDevice} from '@zxing/library';

export default class App extends Vue {
    public codeReader: BrowserQRCodeReader = new BrowserQRCodeReader();

    protected timeBetweenScansMillis: number = 500;
    protected selectedDeviceId: string = '';
    protected lastResult: string = '';
    protected videoInputDevices: VideoInputDevice[] = [];
    protected lastError = '';

    public mounted() {
        this.codeReader = new BrowserQRCodeReader(this.timeBetweenScansMillis);
        this.getVideoInputDevices();
    }

    public getVideoInputDevices() {
        this.codeReader.getVideoInputDevices()
            .then((videoInputDevices) => {
                this.videoInputDevices = videoInputDevices;
                if (videoInputDevices.length !== 0) {
                    // use the 1st device as default
                    this.selectedDeviceId = videoInputDevices[0].deviceId;
                }
            })
            .catch((err) => {
                this.lastError = `this.codeReader.getVideoInputDevices() return:${err.toString()}`;
            });
    }

    public startQrCamera() {
        this.codeReader.decodeFromInputVideoDevice(this.selectedDeviceId, 'video')
            .then((result) => {
                this.lastResult = result.getText() + '</br>' + this.lastResult;
                this.codeReader.reset();
                this.startQrCamera();
            }).catch((err) => {
            this.lastError = `this.codeReader.decodeFromInputVideoDevice() return:${err.toString()}`;
        });
    }

    public stopQrCamera() {
        this.codeReader.reset();
    }
}
